* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 14px;
  position: relative;
  font-family: 'Red Hat Text';
  font-style: normal;
  font-weight: 700;
}
#root {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

div {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

h1 {
  font-size: 2rem;
  color: white;
  margin: 20vh 2vw 7vh 2vw;
  align-self: center;
  text-align: center;
}

.background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background-color:hsl(266, 20%, 14%);
  justify-content: stretch;
  overflow: hidden;
}
.images {
  width: 100%;
  flex-grow: 1;
}
.cards {
  flex-direction: row;
  justify-content: center;
  gap: 3vw;
}
p {
  margin-top: 2.5vh;
  color: hsl(237, 18%, 59%);
  text-align: center;
}
@media (max-width: 750px) {
  * {
    font-size: 10px;
  }
  h1 {
    font-size: 3rem;
  }
  p {
    margin-top: 2.5vh;
    font-size: 1.5rem;
  }
}
@media (max-width: 540px) {
  * {
    font-size: 6px;
  }
  h1 {
    font-size: 3rem;
    margin: 30vh 2vw 7vh 2vw;
  }
  p {
    margin-top: 1.5vh;
    font-size: 1.5rem;
  }
}